import {Component, OnInit} from '@angular/core';
import {Validators, FormBuilder, FormGroup} from '@angular/forms';
import {RouterModule, Routes, Router, ActivatedRoute} from '@angular/router';
import {ToastrService} from 'ngx-toastr';

// Services
import {StudentsService} from '../../../services/students/students.service';
import {routerTransition} from '../../../services/config/config.service';

@Component({
  selector: 'app-student-details',
  templateUrl: './student-details.component.html',
  styleUrls: ['./student-details.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''}
})

export class StudentDetailsComponent implements OnInit {
  index: any;
  studentDetail: any;

  constructor(private router: Router, private route: ActivatedRoute, private studentsService: StudentsService, private toastr: ToastrService) {
    // Get user detail index number sent in params
    this.route.params.subscribe(params => {
      this.index = params['id'];
      if (this.index && this.index !== null && this.index !== undefined) {
        this.getStudentDetails(this.index);
      }
    });
  }

  ngOnInit() {
  }

  // Get students details
  getStudentDetails(index: number) {
    const getStudentDetail = this.studentsService.getStudentDetails(index);
    if (getStudentDetail) {
      this.studentDetail = getStudentDetail.studentData;
      this.toastr.success(getStudentDetail.message, 'Success');
    }
  }

}



