import {Component, OnInit, OnDestroy, QueryList, ViewChildren} from '@angular/core';
import {takeUntil} from 'rxjs/operators';
import {Subject, Observable} from 'rxjs';
import {DecimalPipe} from '@angular/common';

// Services
import {StudentsService} from '../../../services/students/students.service';
import {routerTransition} from '../../../services/config/config.service';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-student-list',
  templateUrl: './student-list.component.html',
  styleUrls: ['./student-list.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''}
})

export class StudentListComponent implements OnInit, OnDestroy {
  studentList: any;
  studentListData: any;
  status;

  constructor(private studentsService: StudentsService, private toastr: ToastrService) {}

  destroy$: Subject<boolean> = new Subject<boolean>();

  // Call students list function on page load
  ngOnInit() {
    this.getStudentList();
  }

  // Get students list from services
  getStudentList() {
    this.studentsService.getStudents().pipe(takeUntil(this.destroy$)).subscribe((students: any[]) => {
      console.log(students);
      this.studentList = students;
    });
    // const studentList = this.studentsService.getStudents();
    const returnData = {
      code: 200,
      message: 'Students List Was Fetched Successfully',
      data: JSON.parse(localStorage.getItem('students'))
    };
    this.success(returnData);
  }

  // Get students list success
  success(data) {
    this.studentListData = data.data;
    for (let i = 0; i < this.studentListData.length; i++) {
      this.studentListData[i].name = this.studentListData[i].first_name + ' ' + this.studentListData[i].last_name;
    }
  }

  // Delete a students with its index
  deleteStudent(index: number) {
    console.log('id to delete:', index);
    // get confirm box for confirmation
    const r = confirm('Are you sure?');
    if (r === true) {
      const studentDelete = this.studentsService.deleteStudent(index).subscribe(() => this.status = 'Delete successful');
      if (studentDelete) {
        this.toastr.success('Success', 'Student Was Deleted');
      }
      this.getStudentList();
    }
  }

  ngOnDestroy() {
    this.destroy$.next(true);
    this.destroy$.unsubscribe();
  }
}




