import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs';
import {catchError, retry} from "rxjs/operators";

const baseUrl = 'http://localhost:8888/api';

@Injectable()
export class StudentsService {

  constructor(private http: HttpClient) {
  }

  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };

  rootURL = '/api';

  // Get all students list via API or any data storage
  getStudents() {
    let studentList: any;
    if (localStorage.getItem('students') && localStorage.getItem('students') !== '') {
      studentList = {
        code: 200,
        message: 'Students List Was Fetched Successfully',
        data: JSON.parse(localStorage.getItem('students'))
      };
    } else {
      studentList = {
        code: 200,
        message: 'Students List Was Fetched Successfully',
        data: JSON.parse(localStorage.getItem('students'))
      };
    }
    return this.http.get(this.rootURL + '/getStudents');
  }

  getStudentDetails(index: number) {
    const studentList = JSON.parse(localStorage.getItem('students'));

    const returnData = {
      code: 200,
      message: 'Student Details Were Fetched',
      studentData: studentList[index]
    };

    return returnData;
  }

  createStudent(student: any) {
    const studentList = JSON.parse(localStorage.getItem('students'));
    student.id = this.generateRandomID();
    studentList.unshift(student);
    localStorage.setItem('students', JSON.stringify(studentList));
    return this.http.post(`${baseUrl}/createStudent`, {student});

  }

  updateStudent(student: any, index) {
    const studentList = JSON.parse(localStorage.getItem('students'));
    studentList[index] = student;
    localStorage.setItem('students', JSON.stringify(studentList));
    return this.http.post(`${baseUrl}/updateStudent`, {student});
  }

  deleteStudent(id): Observable<any> {
    const studentList = JSON.parse(localStorage.getItem('students'));
    studentList.splice(id, 1);
    localStorage.setItem('students', JSON.stringify(studentList));
    return this.http.delete(baseUrl + '/deleteStudent/' + id, this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.handleError)
      );
  }

  // Error handling
  handleError(error) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Get client-side error
      errorMessage = error.error.message;
    } else {
      // Get server-side error
      errorMessage = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    window.alert(errorMessage);
    return errorMessage;
  }

  generateRandomID() {
    const x = Math.floor((Math.random() * Math.random() * 9999));
    return x;
  }

}



